<template>
  <div>
    <div v-if="isInstallMetaMask">
      <div>
        메타마스크 설치됨
      </div>
  
      <div v-if="myAddress == null">
        <button v-on:click="connectToMetaMask">메타마스크 연결</button>
      </div>
      <div v-else>
        <!--메타마스크 연결이 되었을 때,  -->
        <div v-if="myContract == null ">
          <button v-on:click="connectToContract">컨트랙트 연결</button>
        </div>
        <div v-else>
          <div class="root"> <!-- 최상위 DIV -->
            <!-- 컨트랙트까지 연결이 되었다 -->
            <div class="functionCard">
              <!-- 이름이 들어갈 DIV -->
              <div class="functionName">
                ownerOf
              </div>

              <!-- 인풋이 들어갈 DIV -->
              <div class="functionInputRoot">
                <div class="flex">
                  <div class="paramName">
                    tokenId :
                  </div>
                  <input class="paramInput" type="text" placeholder="tokenId" v-model="ownerOf_tokenId">
                </div>
              </div>
              <!-- 버튼이 들어갈 DIV -->
              <div class="functionSubmit flex">                
                <button class="submitButton" v-on:click="requestOwnerOf">onwerof 요청</button>                
              </div>
              <!-- 결과값이 들어갈 DIV -->
              <div class="functionResult">
                <!-- ownerOf 결과값 -->
                {{ownerOf_result}}
              </div>
            </div>
            <!-- balanceOf 구현 -->
            <div class="functionCard">              
              <!-- 이름이 들어갈 DIV -->
              <div class="functionName">
                balanceOf
              </div>

              <div class="functionInputRoot">
                <div class="flex">
                  <div class="paramName">
                    address :
                  </div>
                  <input class="paramInput" type="text" placeholder="address" v-model="balanceOf_address">  
                </div>
              </div>
              <!-- balanceOf 구현 -->
              <div class="functionSubmit flex">                
                <button class="submitButton" v-on:click="requestBalanceOf">balanceOf 요청</button>
              </div>

              <!-- balanceOf 결과값 -->
              <div class="functionResult">
                {{balanceOf_result}}
              </div>
            </div>
            <!-- mint 구현 -->
            <div class="functionCard">                         
              <!-- 이름이 들어갈 DIV -->
              <div class="functionName">
                mint
              </div>
              
              <div class="functionInputRoot">
                <div class="flex">
                  <div class="paramName">
                    to :
                  </div>
                  <input class="paramInput" type="text" placeholder="to" v-model="mint_to">
                </div>
                <div class="flex">
                  <div class="paramName">
                    tokenId :
                  </div>
                  <input class="paramInput" type="text" placeholder="tokenId" v-model="mint_tokenId">
                </div>
                <div class="flex">
                  <div class="paramName">
                    uri :
                  </div>                  
                  <input class="paramInput" type="text" placeholder="uri" v-model="mint_uri">
                </div>
              </div>

              <!-- mint 구현 -->
              <div class="functionSubmit flex">                
                <button class="submitButton" v-on:click="requestMint">mint 요청</button>
              </div>
              
              <div class="functionResult">
                <!-- mint 결과값 -->
                {{mint_result}}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      메타 마스크 설치안됨
    </div>
  </div>
</template>

<script>
import {ethers} from "ethers";
import abi from "../assets/abi.json";

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data(){
    return{
      isInstallMetaMask : window.ethereum != null 
                          && window.ethereum.isMetaMask == true,
      myAddress : null,
      myContract : null,

      ownerOf_tokenId : null,
      ownerOf_result : null,
      
      balanceOf_address : null,
      balanceOf_result : null,

      mint_to : null,
      mint_tokenId : null,
      mint_uri : null,
      mint_result : null,
    }
  },
  methods:{
    async connectToMetaMask()
    {
      console.log('click connectToMetaMask');
      var addressArray = await window.ethereum.request( { method : "eth_requestAccounts" } );
      
      console.log(addressArray);

      // 주소값이 배열로 들어있다.
      if ( addressArray.length > 0 )
      {
        this.myAddress = addressArray[0];
      }
    },
    connectToContract()
    {
      console.log("click connectToContract");
      var contractAddress = "0x2fbcd49a8ed083b338abd8a5dfe321b5faeb27d3";

      var provider = new ethers.providers.Web3Provider(window.ethereum);
      var signer = provider.getSigner();
      this.myContract = new ethers.Contract(contractAddress, abi, signer);
      console.log(this.myContract);
    },
    async requestOwnerOf()
    {
      console.log("click requestOwnerof");
      console.log(this.ownerOf_tokenId);
      this.ownerOf_result = await this.myContract.ownerOf(this.ownerOf_tokenId);
      console.log(this.ownerOf_result);
    },
    async requestBalanceOf()
    {
      console.log("click requestBalanceOf");
      console.log(this.balanceOf_address);
      this.balanceOf_result = await this.myContract.balanceOf(this.balanceOf_address);
      console.log(this.balanceOf_result);
    },
    async requestMint()
    {
      console.log("click requstMint");
      console.log(this.mint_to);
      console.log(this.mint_tokenId);
      console.log(this.mint_uri);
      this.mint_result = await this.myContract.mint(
        this.mint_to, this.mint_tokenId, this.mint_uri);
      console.log(this.mint_result);
    }
    
  }
}
</script>

